.head {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .head__photo {
    margin-top: 1.875rem;
    border-radius: 50%;
    box-shadow: 0 0 0 21px #ffffff, 0 0 0 23px #0270ba; }
  .head__title {
    margin: 0;
    margin-top: 2.85rem;
    text-transform: uppercase;
    text-align: center;
    word-break: break-all; }
  .head__firstname {
    display: block;
    font-size: 3.5rem;
    line-height: 1;
    font-weight: 100; }
    @media (max-width: 1440px) {
      .head__firstname {
        font-size: 3rem; } }
  .head__surname {
    display: block;
    font-size: 4rem;
    line-height: 1; }
  .head__position {
    margin: 0;
    font-weight: bold;
    font-size: 1.58rem;
    line-height: 1.3;
    text-align: center;
    text-transform: uppercase; }
    @media (max-width: 1440px) {
      .head__position {
        font-size: 1.08rem; } }
  .head__add-name {
    margin: 0;
    margin-top: 1.79rem;
    font-size: 1.625rem;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase; }
    @media (max-width: 1440px) {
      .head__add-name {
        font-size: 1.08rem; } }
