.title {
  display: flex;
  min-height: 4.92rem;
  margin: 0;
  padding-left: 5.4rem;
  font-size: 2.7rem;
  line-height: 1;
  color: #0270ba;
  align-items: center;
  text-transform: uppercase;
  background-size: 4.85rem;
  word-break: break-all; }
  @media (max-width: 1440px) {
    .title {
      font-size: 1.96rem;
      line-height: 2.33; } }
  .title_profile {
    background: url(images/profile-icon.svg) 0 0 no-repeat;
    background-size: 4.85rem; }
  .title_contact {
    background: url(images/phone-icon.svg) 0 0 no-repeat;
    background-size: 4.85rem; }
  .title_skills {
    background: url(images/skills-icon.svg) 0 0 no-repeat;
    background-size: 4.85rem; }
  .title_experience {
    background: url(images/experience-icon.svg) 0 0 no-repeat;
    background-size: 4.85rem; }
  .title_education {
    background: url(images/education-icon.svg) 0 0 no-repeat;
    background-size: 4.85rem; }
  .title_software {
    background: url(images/software-icon.svg) 0 0 no-repeat;
    background-size: 4.85rem; }
  .title::after {
    content: "";
    flex-grow: 1;
    flex-shrink: 0;
    margin-left: 0.417rem;
    height: 0.333rem;
    transform: translateY(-100%);
    background: #0270ba; }
