@font-face {
  font-family: "Myriad Pro";
  src: local("MyriadPro"), local("MyriadPro"), url(fonts/MyriadPro-Regular.woff) format("woff"), url(fonts/MyriadPro-Regular.ttf) format("truetype"), url(fonts/MyriadPro-Regular.svg#webfont) format("svg");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Myriad Pro";
  src: local("MyriadPro"), local("MyriadPro"), url(fonts/MyriadPro-Bold.woff) format("woff"), url(fonts/MyriadPro-Bold.ttf) format("truetype"), url(fonts/MyriadPro-Bold.svg#webfont) format("svg");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "Myriad Pro";
  src: local("MyriadPro"), local("MyriadPro"), url(fonts/MyriadPro-LightSemiExtended.woff) format("woff"), url(fonts/MyriadPro-LightSemiExtended.ttf) format("truetype"), url(fonts/MyriadPro-LightSemiExtended.svg#webfont) format("svg");
  font-weight: 100;
  font-style: normal; }

html {
  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1.208;
  color: #000000; }

@media (max-width: 1440px) {
  body {
    font-size: 0.75rem; } }

.main {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .main {
      display: block; } }

.first-column {
  margin-top: 5.5%;
  margin-left: 4.5%;
  width: 35.4%; }
  .first-column__profile {
    width: 94.6%;
    margin-top: 1.13rem; }
    @media (max-width: 1024px) {
      .first-column__profile {
        width: 100%; } }
  .first-column__contacts {
    width: 94%;
    margin-top: 3rem; }
    @media (max-width: 1024px) {
      .first-column__contacts {
        width: 100%; } }
  .first-column__skills {
    width: 94%;
    margin-top: 1.083rem; }
    @media (max-width: 1024px) {
      .first-column__skills {
        width: 100%; } }
  @media (max-width: 1280px) {
    .first-column {
      width: 43%;
      margin-left: 2.5%; } }
  @media (max-width: 1024px) {
    .first-column {
      width: 90%;
      margin: 0 5%; } }

.second-column {
  margin-top: 5.7%;
  margin-right: 4.5%;
  width: 51.5%; }
  .second-column__experience {
    margin-top: 3.54rem; }
  .second-column__software {
    margin-top: 3.55rem; }
  @media (max-width: 1440px) {
    .second-column {
      width: 50%; } }
  @media (max-width: 1280px) {
    .second-column {
      width: 45%;
      margin-right: 2.5%; } }
  @media (max-width: 1024px) {
    .second-column {
      width: 90%;
      margin: 0 5%; } }
